/**
 * 获取验房报修列表 (新)
 * @param {*} params 
 */
const getCheckHouseListNew = (params) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-repair/complQuery/getWorkOrderList', params, true).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}

export { 
    getRoomsList,
    getRoomsDetail,
    newCheckHouse,
    getCheckHouseList,
    getCheckHouseListNew
};

