<template>
    <div class="handCheck" >
        <navigation-bar pageName="提交验房" @onLeftClick="goBack">
          <slot slot="nav-right" name="right" >
            <div slot="right" class="nav-right">
              <div @click="goRepair">报修</div>
            </div>
          </slot>
        </navigation-bar>
        <div class="detail-box">
            <div class="title">
                <div class="left">
                    <img :src="floorIcon" alt="">
                    <span>{{roomDetail.BUILD_NAME}} {{roomDetail.UNIT_NO}} {{roomDetail.ROOM_NAME}}</span>
                </div>
                <div class="right">
                    <img :src="stayCheckIcon2" alt="">
                </div>
            </div>
            <van-field
                    readonly
                    clickable
                    label="交付时间"
                    :value="showTime"
                    input-align="right"
                    placeholder="请选择交付时间"
                    right-icon="arrow"
                    @click="showDatePicker = true"/>
            <div class="img-box">
                <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" ref="addImageComponent"></add-image>
            </div>
            <div class="pass-btn" @click="passCheck">通过</div>
        </div>
        <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          round
          close-on-click-action
          @select="addSelect"/>
        <van-popup v-model="showDatePicker" position="bottom">
            <van-datetime-picker
                    v-model="workDate"
                    type="date"
                    title="选择交付时间"
                    :min-date="minDate"
                    :max-date="maxDate"
                    @cancel="showDatePicker = false"
                    @confirm="selectDate"
            />
        </van-popup>
    </div>
</template>

<script>
import {
  newCheckHouse
} from "../../services/checkHouseService";
import { store } from "../../utils/store";
import AddImageVue from "../addImage/AddImage.vue";
import floorIcon from "@/assets/imgs/checkHouse/Floor.png";
import stayCheckIcon from "@/assets/imgs/checkHouse/stayCheck.png";
import stayCheckIcon2 from "@/assets/imgs/checkHouse/stayCheck2.png";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "handCheck",
  data() {
    return {
      // 传递的参数
      roomDetail: null,
      userInfo: null,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      imgList: [],
      showAddSheet: false,

      floorIcon: floorIcon,
      stayCheckIcon: stayCheckIcon,
      stayCheckIcon2: stayCheckIcon2,
      showTime: null,
      showDatePicker: false,
      workDate: new Date(),
      minDate: new Date(1919, 0, 1),
      maxDate: new Date(2090, 11, 31),
    };
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  created: function() {
    this.userInfo = store.get("userInfo");
    this.roomDetail = this.$route.params;
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  activated() {},
  methods: {
      /**
       * 选择日期事件
       */
      selectDate: function(value) {
          this.showDatePicker = false;
          this.showTime = this.formatter(value);
          this.workDate = value;
      },

      /**
       * 格式化日期
       */
      formatter: function(date) {
          return (
              date.getFullYear() +
              "-" +
              this.getTime(date.getMonth() + 1) +
              "-" +
              this.getTime(date.getDate())
          );
      },

      /**
       * 获取规则的格式
       */
      getTime(time) {
          if (time < 10) {
              return "0" + time;
          } else {
              return time;
          }
      },


      /**
     * 修改详情
     */
    goRepair: function() {
      this.roomDetail.flag = 1;
      this.$router.replace({
        name: "newRepair",
        params: this.roomDetail
      });
    },

    /**
     * 页面返回事件
     */
    goBack: function() {
      store.set("reloadCheckHouseStatus", 0);
      this.$router.back(-1);
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData) {
      this.imgList = newImageData;
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex
        ? this.$refs.addImageComponent.getPicture()
        : this.$refs.addImageComponent.useCamera();
    },

    /**
     * 修改验房详情
     * @param
     **/
    passCheck: function() {
      if (!this.hasCompleteParam()) {
        return;
      }
      var handData = {
        ROOM_CODE: this.roomDetail.ROOM_CODE,
        USER_CODE: this.userInfo.USER_CODE,
        DELIVE_FLAG: this.roomDetail.DELIVE_FLAG,
        PIC_URL: this.imgList,
        FILETEXT: "",
        DELIVERY_DATE: this.showTime,
      };
      newCheckHouse({
        data: JSON.stringify(handData)
      }).then(
        res => {
          store.set("reloadCheckHouseStatus", 2);
          store.set("checkListSingleData", {
            ROOM_CODE: this.roomDetail.ROOM_CODE,
            DELIVE_FLAG: "1",
          });
          this.$router.back(-1);
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      if (this.imgList.length <= 0) {
        this.$toast("请选择图片！");
        return false;
      }
      return true;
    }
  },
  computed: {}
};
</script>
